<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="12">
          <mrc_ess_multiText label="Swift Name"
                             :error-messages="$errors('list','swift_config_name',requiredRule)"
                             :isNational="judegNational('swift_config_name')"
                             v-model="list.swift_config_name"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
        <v-col cols="12"
               class="mb-5">
          <h5 style="font-size:20xpx;color:rgb(153, 153, 153)">Prefix<span>?</span></h5>
        </v-col>
        <v-col cols="12">
          <v-card style="border:1px solid #e0e0e0">
            <v-card-text>
              <v-row>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_prefix_first',requiredRule)" -->
                  <mrc_ess_multiText label="First Value"
                                     :isNational="judegNational('swift_config_prefix_first')"
                                     v-model="list.swift_config_prefix_first"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_prefix_object',requiredRule)" -->
                  <mrc_ess_multiText label="Object"
                                     :isNational="judegNational('swift_config_prefix_object')"
                                     v-model="list.swift_config_prefix_object"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_prefix_second',requiredRule)" -->
                  <mrc_ess_multiText label="Second Value"
                                     :isNational="judegNational('swift_config_prefix_second')"
                                     v-model="list.swift_config_prefix_second"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="4"
               class="mt-7 mb-7">
          <mrc_ess_NumInput :readonly="!disabled"
                            label='Length'
                            v-model="list.swift_config_length"></mrc_ess_NumInput>
        </v-col>
        <v-col cols="4"
               class="mt-7 mb-7">
          <mrc_ess_NumInput :readonly="!disabled"
                            label='Start Index'
                            v-model="list.swift_config_start_index"></mrc_ess_NumInput>
        </v-col>
        <v-col cols="4"></v-col>
        <v-col cols="12"
               class="mb-5">
          <h5 style="font-size:20xpx;color:rgb(153, 153, 153);">Postfix<span>?</span></h5>
        </v-col>
        <v-col cols="12">
          <v-card style="border:1px solid #e0e0e0">
            <v-card-text>
              <v-row>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_suffix_first',requiredRule)" -->
                  <mrc_ess_multiText label="First Value"
                                     :isNational="judegNational('swift_config_suffix_first	')"
                                     v-model="list.swift_config_suffix_first	"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_suffix_object',requiredRule)" -->
                  <mrc_ess_multiText label="Object"
                                     :isNational="judegNational('swift_config_suffix_object')"
                                     v-model="list.swift_config_suffix_object"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
                <v-col cols="6">
                  <!--                                  :error-messages="$errors('list','swift_config_suffix_second',requiredRule)" -->
                  <mrc_ess_multiText label="Second Value"
                                     :isNational="judegNational('swift_config_suffix_second')"
                                     v-model="list.swift_config_suffix_second"
                                     :readonly="disabled">
                  </mrc_ess_multiText>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt label="Table*"
                          :errMsg="$errors('list','swift_config_table',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="list.swift_config_table"
                          obj="mrbase_sys_datamodel_table">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="6">
          <mrc_ess_prompt label="Field*"
                          :errMsg="$errors('list','swift_config_field',requiredRule)"
                          :rules='false'
                          :disabled='disabled'
                          v-model="list.swift_config_field"
                          obj="swift_config_field">
          </mrc_ess_prompt>
        </v-col>
        <v-col cols="12">
          <mrc_ess_multiText label="Company*"
                             :error-messages="$errors('list','org_level_uuid',requiredRule)"
                             :isNational="judegNational('org_level_uuid')"
                             v-model="list.org_level_uuid"
                             :readonly="disabled">
          </mrc_ess_multiText>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       @cancel='cancel'
                       :loading='btnIsLoading'
                       v-if='!disabled'></mrc_ess_submitBtn>
  </v-card>
</template>

<script> 
import {
  getserialNumberList
  , createSerialNumber
  , detailSerialNumber
  , addSerialNumber
  , updateSerialNumber
  , deleteSerialNumber
} from '@/service/api/serialNumber.js'
import { getBusinessList } from '@/service/api/prompt.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      list: {},
      btnIsLoading: false,
    };
  },
  computed: {
  },
  created () { },
  watch: {},
  async mounted () {
    if (this.$store.state.uuid) {
      await this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    async getDetail () {
      const res = await detailSerialNumber({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
      else {

      }
    },
    async success () {
      console.log(this.list);
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      this.btnIsLoading = true
      if (this.$store.state.uuid) {
        updateSerialNumber(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
          this.btnIsLoading = false
        })
      }
      else {
        addSerialNumber(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'serial-number' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
          this.btnIsLoading = false
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'serial-number'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      swift_config_name: {
        required
      },
      swift_config_prefix_first: {

      },
      swift_config_prefix_object: {

      },
      swift_config_prefix_second: {

      },
      swift_config_length: {
        required
      },
      swift_config_start_index: {

      },
      swift_config_suffix_first: {

      },
      swift_config_suffix_object: {

      },
      swift_config_suffix_second: {

      },
      swift_config_table: {
        required
      },
      swift_config_field: {
        required
      },
      org_level_uuid: {
        required
      },
    }
  },
};
</script>

<style scoped>
</style>

